import React from 'react';
import { BrowserRouter as Router,
  Switch,Route } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { Layout, Typography, Space } from 'antd';

import { Exchanges, Homepage, News, Cryptocurrencies, CryptoDetails, Navbar, Downloads, Team } from './components';
import './App.css';

const App = () => (
  <div className="app">
    <div className="navbar">
      <Navbar />
    </div>
    <div className="main">
      <Layout> 
        <div className="routes">
          <Switch>
            <Route exact path="/">
              <Homepage />
            </Route>
            <Route exact path="/exchanges">
              <Exchanges />
            </Route>
            <Route exact path="/cryptos">
              <Cryptocurrencies />
            </Route>
            <Route exact path="/crypto/:coinId">
              <CryptoDetails />
            </Route>
            <Route exact path="/news">
              <News />
            </Route>
            <Route exact path="/downloads">
              <Downloads />
            </Route>
            <Route exact path="/team">
              <Team />
            </Route>
          </Switch>
        </div>
      </Layout>
      <div className="footer">
        <Typography.Title level={5} style={{ color: '#0336FF', textAlign: 'center' }} >Developed with &hearts; by &nbsp;
          <Link style={{ color: '#FFDE03'}} to="/">
             Team Yen Crypto
          </Link> <br />
        </Typography.Title>
        <Space>
          <Link style={{ color: '#FFDE03'}} to="/">Home</Link>
          <Link style={{ color: '#FFDE03'}} to="/news">News</Link>
          <Link style={{ color: '#FFDE03'}} to="/team">Team</Link>
        </Space>
      </div>
    </div>
  </div>
);

export default App;