import React, { useState, useEffect } from 'react';
import { Button, Menu, Typography, Avatar } from 'antd';
import { Link } from 'react-router-dom';
import { HomeOutlined, MoneyCollectOutlined, BulbOutlined, FundOutlined, MenuOutlined, TeamOutlined, DownloadOutlined } from '@ant-design/icons';
import icon from '../images/log1o.png'
const Navbar = () => {
  const [activeMenu, setActiveMenu] = useState(true);
  const [screenSize, setScreenSize] = useState(undefined);

  useEffect(() => {
    const handleResize = () => setScreenSize(window.innerWidth);

    window.addEventListener('resize', handleResize);

    handleResize();

    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    if (screenSize <= 800) {
      setActiveMenu(false);
    } else {
      setActiveMenu(true);
    }
  }, [screenSize]);

  return (
    <div className="nav-container">
      <div className="logo-container">
        <img src={icon} style={{width: '140px'}}/>
        <Button className="menu-control-container" onClick={() => setActiveMenu(!activeMenu)}><MenuOutlined /></Button>
      </div>
      {activeMenu && (
      <Menu style={{background: '#0336FF'}}>
        <Menu.Item style={{ color: '#FFDE03'}} icon={<HomeOutlined />}>
          <Link style={{ color: '#FFDE03'}} to="/">Home</Link>
        </Menu.Item>
        <Menu.Item style={{ color: '#FFDE03'}} icon={<FundOutlined />}>
          <Link style={{ color: '#FFDE03'}} to="/cryptos">Cryptocurrencies</Link>
        </Menu.Item>
        <Menu.Item style={{ color: '#FFDE03'}} icon={<BulbOutlined />}>
          <Link style={{ color: '#FFDE03'}} to="/news">News</Link>
        </Menu.Item>
        <Menu.Item style={{ color: '#FFDE03'}} icon={<DownloadOutlined />}>
          <Link style={{ color: '#FFDE03'}} to="/downloads">Download</Link>
        </Menu.Item>
        <Menu.Item style={{ color: '#FFDE03'}} icon={<TeamOutlined />}>
          <Link style={{ color: '#FFDE03'}} to="/Team">Team</Link>
        </Menu.Item>
      </Menu>
      )}
    </div>
  );
};

export default Navbar;
